div.appContent .profilePreview {
	height: 85vh;
	padding: 20px;
	-webkit-box-shadow: 0px 2px 10px 5px #b3b3b3;
	        box-shadow: 0px 2px 10px 5px #b3b3b3;
	border-radius: 10px;
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	z-index: -2;
	color: white;
}

div.appContent .profilePreview .backgroundOverlay {
 	border-radius: 10px;
	position: absolute;
	background: #000;
	opacity: 0.5;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
	z-index: -1;
}

div.profilePreview .avatar {
	/* display: table; */
	width: 122px;
	height: 122px;
	margin: 10px 0px;
}

div.profilePreview .avatar img {
	/* display: table; */
	overflow: hidden;
}

div.profilePreview .username {
	margin: 5px 0px;
	font-size: 1.4em;
}

div.profilePreview .email {
	font-size: 1em;
}

div.profilePreview .username,
div.profilePreview .email {
	text-align: center;
	width: 100%;
	word-wrap: break-word;
}

div.profilePreview .intro {
	width: 100%;
	margin: 20px 0px;
	font-size: 1.1em;
	text-align: justify;
}

div.profilePreview .avatar .circle {
	border-radius: 50%;
	display: -ms-flexbox;
	display: flex;
	overflow: hidden;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

div.profilePreview .avatar .losange {
	-webkit-transform: rotate(45deg) translateY(12px);
	    -ms-transform: rotate(45deg) translateY(12px);
	        transform: rotate(45deg) translateY(12px);
}

div.profilePreview .avatar .losange, .losange div {
    -webkit-transform-origin: 52% 93%;
        -ms-transform-origin: 52% 93%;
            transform-origin: 52% 93%;
    overflow: hidden;
    width: 86px;
    height: 86px;
}

.losange .los1 {
    width: 122px;
    height: 122px;
    -webkit-transform: rotate(-45deg) translateY(-12px) translateX(38px);
        -ms-transform: rotate(-45deg) translateY(-12px) translateX(38px);
            transform: rotate(-45deg) translateY(-12px) translateX(38px);
}

div.profilePreview .avatar .diamond {
    -webkit-transform: rotate(45deg) translateY(-9px) translateX(4px);
        -ms-transform: rotate(45deg) translateY(-9px) translateX(4px);
            transform: rotate(45deg) translateY(-9px) translateX(4px);
}

div.profilePreview .avatar .diamond .dia {
    width: 122px;
    height: 122px;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

div.profilePreview .avatar .diamond {
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
    width: 105px;
    height: 105px;
}

div.profilePreview .hexa {
    width: 170px;
    height: 100px;
    -webkit-transform: rotate(120deg) translateX(25px) translateY(12px);
        -ms-transform: rotate(120deg) translateX(25px) translateY(12px);
            transform: rotate(120deg) translateX(25px) translateY(12px);
}

div.profilePreview .hexa div {
    width: 100%;
    height: 100%;
}

div.profilePreview .hexa, .hexa div {
    margin: 0 auto;
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
}

div.profilePreview .hex1 {
    -webkit-transform: rotate(-60deg);
        -ms-transform: rotate(-60deg);
            transform: rotate(-60deg);
}

div.profilePreview .hex2 {
    -webkit-transform: rotate(-60deg);
        -ms-transform: rotate(-60deg);
            transform: rotate(-60deg);
}

div.profilePreview .hexa img {
    position: relative;
	top: -15px;
    left: 24px;
}

div.profilePreview .octo {
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

div.profilePreview .octo, .octo div {
    margin: 0 auto;
    -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    overflow: hidden;
    width: 122px;
    height: 122px;
}

div.profilePreview .octo1 {
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

div.profilePreview .hobbies {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
}

div.profilePreview .hobby {
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid white;
    -webkit-box-shadow: 0px 0px 10px 0px #b3b3b3;
            box-shadow: 0px 0px 10px 0px #b3b3b3;
    -webkit-animation: fadein 1s ease-out;
            animation: fadein 1s ease-out;
    background-color: #00000060;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


